import React, { useState, useRef } from "react";
import Another from "./Another";
import Kind from "./Kind";
import { useParams, useNavigate } from "react-router-dom";

import StoryPic from "./StoryPic";
const Main = () => {

  const [mute, setMute] = useState(true);
  const { person } = useParams();
  const navigate = useNavigate();
  const [page, SetPage] = useState({
    Kind: { show: "block", kind: "" },
    StoryPic: { show: "none" },
    Another: { show: "none" },
  });
  
  const ToKind = () => {
    SetPage({
      Kind: { show: "block", kind: "" },
      StoryPic: { show: "none" },
      Another: { show: "none" },
    });
  };
  const ToStoryPic = (kind) => {
    console.log(kind);
    SetPage({
      ...page,
      Kind: { show: "none", kind: kind },
      StoryPic: { show: "block" },
      Another: { show: "none" },
    });
  };
  const ToAnother = () => {
    SetPage({
      ...page,
      StoryPic: { show: "none" },
      Another: { show: "block" },
    });
  };
  const ToSend = () => {
    navigate(`/letter?${page.Kind.kind ?? "Born"}&person=${person}`);
  };
  return (
    <>
     
      {/* <button onClick={()=>setMute(!mute)}>test code</button> */}
      <Kind
        display={page.Kind.show}
        ShowNext={ToStoryPic}
        person={person ?? "Girl"}
      />
      <StoryPic
        display={page.StoryPic.show}
        ToBack={ToKind}
        ShowNext={ToAnother}
        kind={page.Kind.kind}
      />
      <Another
        display={page.Another.show}
        ToSend={ToSend}
        ShowNext={ToStoryPic}
      />
    </>
  );
};

export default Main;
